import evanFormItem from '../../components/evan-form/evan-form-item.vue';
import evanForm from '../../components/evan-form/evan-form.vue'; 
import { mapState } from 'vuex';
export default {
	components: {
		evanFormItem,
		evanForm
	},
	data() {
		return {
			info:{
				realName:'',
				phoneNumber:'',
				company:'',
			},
			rules:{
				realName: {
					required: true,
					message: '请输入姓名'
				},
				phoneNumber: [{
						required: true,
						message: '请输入电话'
					},
					{
						validator: (rules, tel, callback) => {
							if (this.$validate.isMobilePhone(tel)) {
								callback();
							} else {
								callback(new Error('手机号格式不正确'));
							}
						}
					}
				],
				company: {
					required: true,
					message: '请输入门店'
				},
			}
		}
	},
	computed: {
		...mapState(['user'])
	},
	onShow() {
		console.log(this.user)
		let _this = this
		for(let key in _this.info){
			let temp = _this.user.userInfo[key]
			if(!temp && typeof(temp)!="undefined" && temp!=0){
				_this.info[key] = ""
			}else{
				_this.info[key] = temp
			}
			
		}
		_this.$nextTick(() => {
			_this.$refs.form.setRules(_this.rules)
		})
	},
	methods: {
		save(type){
			console.log(type)
			let _this = this
			_this.$refs.form.validate(res => {
				if (res) {
					let temp = JSON.parse(JSON.stringify( _this.user.userInfo))
					for(let key in _this.info){
						temp[key] = _this.info[key]
					}
					if(type){
						temp.authStatus = 2
					}
					_this.$request.applyAuth(temp).then((res)=>{
						console.log(res)
						if(type){
							uni.showToast({
								title:'申请授权成功，请耐心等候',
								icon:'none',
								duration:2000
							})
						}else{
							uni.showToast({
								title:'保存成功',
								duration:2000
							})
						}
						_this.$store.commit('SET_USERINFO',res.data.data)
					})
				}
			})
		}
	}
}
